@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desctop-min-width) {
    @content;
  }
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 16px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet() {
    --columns: 8;
    column-gap: 24px;
  }

  @include onDesktop() {
    --columns: 8;
    column-gap: 24px;
  }
}

@mixin PaddingInlain {
  padding-inline: 16px;

  @include onTablet() {
    padding-inline: 44px;
  }
}

@mixin hover {
  transition: 0.3s;

  &:hover {
    transform: scale(1.2);
  }
}
